import Prompt from '@/components/zz-prompt/index.vue'
export default{
	components:{
		Prompt
	},
	data(){
		return{
			promptVisible:false,
			data:{},
			orderno:'',
			uid:'',
			token:'',
			form:{
				contact:'',
				mobile:'',
				address:''
			}
		}
	},
	onLoad(p){
		var userInfo=uni.getStorageSync('userInfo'),that=this
		if(userInfo!='' & userInfo!=null & userInfo!=undefined){
			this.uid=userInfo.uid
			this.token=userInfo.token
		}
		this.orderno=p.orderno
		this.getDetail()
	},
	methods:{
		toFee(){
			uni.navigateTo({
				url:'/pages/myActivity/feeDetail'
			})
		},
		toPromoteFee(){
			uni.navigateTo({
				url:'/pages/myActivity/promoteFeeDetail'
			})
		},
		openchange(){
			this.form={
				contact:this.data.contact,
				mobile:this.data.addrmobile,
				address:this.data.address
			}
			this.promptVisible=true
		},
		clickPromptConfirm(){
			this.$loading()
			var that=this
			var params={
			  uid:this.uid,
			  token:this.token,
			  orderno:this.orderno,
			  contact:this.form.contact,
			  mobile:this.form.mobile,
			  address:this.form.address
			}
			  var url='&r=api.myshop.orders.changeAddr'
			  this.$apiPost(url,params).then((res) =>{
				  that.$msg('修改成功')
				  uni.hideLoading()
				  setTimeout(function(){
					  that.promptVisible=false
					  that.getDetail()
				  },500)
			  })
		},
		getDetail(){
			this.$loading()
			var that=this
			var params={
			  uid:this.uid,
			  token:this.token,
			  orderno:this.orderno,
			}
			  var url='&r=api.myshop.orders.detail'
			  this.$apiPost(url,params).then((res) =>{
				  that.data=res.data
				  uni.hideLoading()
			  })
		}
	}
}
